import Cookies from 'js-cookie'

const authToken = {
  // Token
  // jumpAuthPage  Token
  // getNewToken  TokenToken ()
  tokenTimeoutMethod: 'getNewToken',

  // Cookiekey
  loginKey: 'isLogin',

  // Token
  hasToken: function () {
    return Cookies.get('token')
  },

  // 
  isLogin: function () {
    return Cookies.get(this.loginKey)
  },

  // userid
  setUserId: function (token) {
    // token
    var maxAge = new Date(new Date().getTime() + 30 * 60 * 1000)
    Cookies.set('UserId', token, {
      expires: maxAge
    })
  },

  setRoleId: function (token) {
    // token
    var maxAge = new Date(new Date().getTime() + 30 * 60 * 1000)
    Cookies.set('role_id', token, {
      expires: maxAge
    })
  },

  // 
  setLoginStatus: function () {
    // 
    var maxAge = new Date(new Date().getTime() + 30 * 60 * 1000)
    Cookies.set(this.loginKey, 'true', {
      expires: maxAge
    })
  },

  // Token
  removeToken: function () {
    Cookies.remove('token')
  },

  // 
  removeLoginStatus: function () {
    Cookies.remove(this.loginKey)
  }
}

export default authToken


