import LoadComponent from './importfile'
const main = LoadComponent('main/index')
const staticRoutes = [
  {
    path: '/',
    redirect: '/login',
    component: LoadComponent('login/index')
  },
  {
    path: '/login',
    name: 'login',
    component: LoadComponent('login/index')
  },
  {
    path: '/index',
    redirect: '/index/dashbodrd',
    component: main,
    children: [{
      path: 'dashbodrd',
      name: 'dashbodrd',
      component: LoadComponent('index/index')
    }]
  },
  {
    path: '/loan',
    redirect: '/loan/index',
    component: main,
    meta: { icon: 'loan', title: '', hidden: false },
    children: [
      {
        path: 'index',
        name: 'loanindex',
        component: LoadComponent('loan/index'),
        meta: { title: '', hidden: false }
      },
      {
        path: 'insert',
        name: 'loaninsert',
        component: LoadComponent('loan/insert'),
        meta: { title: '', hidden: false }
      }
    ]
  },
  {
    path: '/card',
    redirect: '/card/index',
    component: main,
    meta: { icon: 'card', title: '', hidden: false },
    children: [
      {
        path: 'index',
        name: 'cardindex',
        component: LoadComponent('card/index'),
        meta: { title: '', hidden: false }
      },
      {
        path: 'insert',
        name: 'cardinsert',
        component: LoadComponent('card/insert'),
        meta: { title: '', hidden: false }
      }
    ]
  },
  {
    path: '/registeruser',
    redirect: '/registeruser/index',
    component: main,
    meta: { icon: 'register', title: '', hidden: false },
    children: [
      {
        path: 'index',
        component: LoadComponent('registeruser/index'),
        name: 'registeruserindex',
        meta: { title: '', hidden: false }
      }
    ]
  },
  {
    path: '/member',
    redirect: '/member/index',
    component: main,
    meta: { icon: 'member', title: '', hidden: false },
    children: [
      {
        path: 'index',
        component: LoadComponent('member/index'),
        name: 'memberindex',
        meta: { title: '', hidden: false }
      }
    ]
  },
  {
    path: '/company',
    component: main,
    meta: { icon: 'company', title: '', hidden: false },
    children: [
      {
        path: 'about',
        component: LoadComponent('company/about'),
        name: 'companyabout',
        meta: { title: '', hidden: false }
      },
      {
        path: 'relief',
        name: 'companyrelief',
        component: LoadComponent('company/relief'),
        meta: { title: '', hidden: false }
      },
      {
        path: 'contact',
        name: 'companycontact',
        component: LoadComponent('company/contact'),
        meta: { title: '', hidden: false }
      }
    ]
  },
  {
    path: '/notice',
    component: main,
    meta: { icon: 'notice', title: '', hidden: false },
    children: [
      {
        path: 'index',
        name: 'noticeindex',
        component: LoadComponent('notice/index'),
        meta: { title: '', hidden: false }
      },
      {
        path: 'insert',
        name: 'noticeinsert',
        component: LoadComponent('notice/insert'),
        meta: { title: '', hidden: false }
      }
    ]
  },
  {
    path: '/banner',
    name: 'banner',
    component: main,
    meta: { title: 'banner', hidden: false },
    children: [
      {
        path: 'index',
        name: 'bannerindex',
        component: LoadComponent('banner/index'),
        meta: { title: 'banner', hidden: false }
      },
      {
        path: 'insert',
        name: 'bannerinsert',
        component: LoadComponent('banner/insert'),
        meta: { title: 'banner', hidden: false }
      }
    ]
  },
  {
    path: '/ADPosition',
    name: 'ADPosition',
    component: main,
    meta: { title: '', hidden: false },
    children: [
      {
        path: 'index',
        name: 'ADPositionindex',
        component: LoadComponent('ADPosition/index'),
        meta: { title: '', hidden: false }
      },
      {
        path: 'insert',
        name: 'ADPositioninsert',
        component: LoadComponent('ADPosition/insert'),
        meta: { title: '', hidden: false }
      }
    ]
  },
  {
    path: '/platform',
    name: 'platform',
    component: main,
    meta: { title: '', hidden: false },
    children: [
      {
        path: 'index',
        name: 'platformindex',
        component: LoadComponent('platform/index'),
        meta: { title: '', hidden: false }
      },
      {
        path: 'insert',
        name: 'platforminsert',
        component: LoadComponent('platform/insert'),
        meta: { title: '', hidden: false }
      }
    ]
  },
  {
    path: '/bank',
    name: 'bank',
    component: main,
    meta: { title: '', icon: 'bank', hidden: false },
    children: [
      {
        path: 'index',
        name: 'bankindex',
        component: LoadComponent('bank/index'),
        meta: { title: '', hidden: false }
      },
      {
        path: 'insert',
        name: 'bankinsert',
        component: LoadComponent('bank/insert'),
        meta: { title: '', hidden: false }
      }
    ]
  },
  {
    path: '/newuser',
    name: 'newuser',
    component: main,
    meta: { title: '', icon: 'newuser', hidden: false },
    children: [
      {
        path: 'index',
        name: 'newuserindex',
        component: LoadComponent('newuser/index'),
        meta: { title: '', hidden: false }
      },
      {
        path: 'insert',
        name: 'newuserinsert',
        component: LoadComponent('newuser/insert'),
        meta: { title: '', hidden: false }
      }
    ]
  },
  {
    path: '*',
    redirect: '/401'
  },
  {
    path: '/401',
    redirect: '/error/401',
    component: main,
    children: [{
      path: '/error/401',
      component: LoadComponent('error/401')
    }]
  }
]

export default staticRoutes


